.card {
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.452) 0px 3px 10px;
  min-height: 138px;
  padding-top: 10px;
}

.card-image-wrapper {
  display: flex;
  justify-content: center;
  min-height: 96px;

  img {
    -webkit-user-drag: none;
    user-select: none;
  }
}

.card-info {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-bottom: 15px;
  border-radius: 0 0 10px 10px;
  user-select: none;
}

.card-text-wrapper {
  display: inline-flex;
  justify-content: center;
  gap: 7px;
  span {
    text-transform: capitalize;
    text-align: center;
    font-size: large;
    padding-bottom: 5px;
  }
}

.card-type-wrapper {
  display: flex;
  gap: 5px;
  justify-content: center;
}

.card-type {
  font-family: monospace;
  font-size: 0.9rem;
  text-transform: uppercase;
  text-align: center;

  box-shadow: rgba(0, 0, 0, 0.644) 1px 2px 2px;
  color: rgb(255, 255, 255);
  border-radius: 5px;
  padding: 5px;
  min-width: 60px;
}

/// Iterate through array of colors
@each $typeColor, $i in $typeColors {
  .card-type.#{$typeColor} {
    background-color: $i;

    &:hover {
      background-color: darken($i, 10%);
    }
  }
}
